@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserrat-regular.woff2") format("woff2"), url("../fonts/montserrat-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserrat-medium.woff2") format("woff2"), url("../fonts/montserrat-medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserrat-semibold.woff2") format("woff2"), url("../fonts/montserrat-semibold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserrat-bold.woff2") format("woff2"), url("../fonts/montserrat-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #011c40;
  line-height: 28px;
  font-size: 16px;
  font-weight: 400; }

img {
  max-width: 100%;
  height: auto; }

.container {
  max-width: 1250px;
  padding-right: 45px;
  padding-left: 45px;
  margin-right: auto;
  margin-left: auto; }

@media (max-width: 1023px) {
  .container {
    max-width: 933px; } }

@media (max-width: 767px) {
  .container {
    max-width: 737px;
    padding-right: 15px;
    padding-left: 15px; } }

.main-logo__ico {
  width: 70px;
  height: 38px;
  fill: #f9fbfd; }

@media (max-width: 1023px) {
  .main-logo__ico {
    width: 38px;
    height: 20px; } }

@media (max-width: 767px) {
  .main-logo__ico {
    width: 42px;
    height: 22px; } }

.header {
  position: relative;
  top: 0;
  padding-top: 35px;
  padding-bottom: 35px;
  background-color: #011c40; }

.header__wrapper {
  display: flex;
  justify-content: space-between; }

.header__logo {
  margin-right: 30px; }

.header__toggle-btn {
  display: none; }

.header__nav {
  margin-top: 6px; }

@media (max-width: 1023px) {
  .header {
    padding-top: 26px;
    padding-bottom: 26px; }
  .header__logo {
    margin-top: 5px; }
  .header__nav {
    margin-top: 0; } }

@media (max-width: 767px) {
  .header {
    padding-top: 15px;
    padding-bottom: 15px; }
    .header--nojs {
      position: static;
      background-color: #f9fbfd; }
    .header--menu-active {
      position: fixed;
      top: 0;
      width: 100%;
      height: 100%;
      background-color: #f9fbfd;
      z-index: 10; }
  .header__wrapper {
    flex-wrap: wrap; }
  .header__logo--nojs {
    margin-bottom: 29px; }
  .header__logo--nojs .header__logo-ico {
    fill: #011c40; }
  .header__logo--menu-active .header__logo-ico {
    fill: #011c40; }
  .header__logo-ico--menu-active {
    fill: #011c40; }
  .header__toggle-btn {
    position: relative;
    display: block;
    padding: 10px;
    width: 34px;
    height: 32px;
    border: none;
    transition: all 0.3s;
    cursor: pointer;
    background-color: #011c40; }
    .header__toggle-btn:hover {
      opacity: 0.9; }
    .header__toggle-btn:active {
      opacity: 0.8; }
    .header__toggle-btn--closed {
      position: fixed;
      top: 15px;
      right: 15px;
      background-color: transparent;
      z-index: 15; }
    .header__toggle-btn--nojs {
      display: none; }
  .header__toggle {
    transition: all 0.3s;
    position: absolute;
    right: 0;
    top: 50%;
    width: 24px;
    height: 2px;
    background-color: #f9fbfd;
    transform: translateY(-50%); }
    .header__toggle::after, .header__toggle::before {
      content: "";
      position: absolute;
      right: 0;
      width: 24px;
      height: 2px;
      background-color: #f9fbfd; }
    .header__toggle::after {
      top: 6px; }
    .header__toggle::before {
      bottom: 6px; }
    .header__toggle--closed {
      top: 50%;
      right: -2px;
      width: 18px;
      height: 2px;
      background-color: #011c40;
      transform: translateY(-50%) rotate(45deg); }
    .header__toggle--closed::after {
      width: 0; }
    .header__toggle--closed::before {
      top: 0;
      width: 18px;
      height: 2px;
      background-color: #011c40;
      transform: rotate(90deg); }
  .header__nav {
    display: none;
    flex-basis: 100%; }
    .header__nav--nojs {
      display: block; }
    .header__nav--menu-active {
      display: block;
      position: fixed;
      top: 78px;
      left: 0;
      width: 100%;
      height: 100%;
      padding-top: 2px;
      padding-left: 15px;
      padding-right: 15px;
      overflow-y: scroll;
      scrollbar-width: none;
      -ms-overflow-style: none; }
    .header__nav--menu-active::-webkit-scrollbar {
      width: 0;
      height: 0;
      background: transparent; } }

.nav-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end; }

.nav-list__link {
  transition: all 0.3s;
  padding: 15px 0 15px 14px;
  margin-right: 20px;
  font-weight: 500;
  text-decoration: none;
  color: #f9fbfd;
  font-size: 16px;
  line-height: 20px; }
  .nav-list__link:hover {
    opacity: 0.8; }
  .nav-list__link:active {
    opacity: 0.6; }

@media (max-width: 1023px) {
  .nav-list__link {
    padding: 10px 0 10px 4px;
    font-size: 14px;
    line-height: 17px; } }

@media (max-width: 767px) {
  .nav-list {
    flex-direction: column; }
  .nav-list__link {
    display: block;
    padding: 10px 15px 10px 0;
    color: #011c40;
    font-size: 18px;
    line-height: 22px; } }

.main {
  flex-grow: 1; }

.social {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end; }

.social__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 23px;
  height: 40px;
  margin-left: 28px; }
  .social__link:hover {
    opacity: 0.8; }
  .social__link:active {
    opacity: 0.6; }

.social__ico--facebook {
  width: 11px;
  height: 20px; }

.social__ico--inst {
  width: 20px;
  height: 20px; }

.social__ico--twitter {
  width: 20px;
  height: 16px; }

@media (max-width: 1023px) {
  .social__link {
    width: 20px;
    margin-left: 16px; }
  .social__ico--facebook {
    width: 9px;
    height: 16px; }
  .social__ico--inst {
    width: 16px;
    height: 16px; }
  .social__ico--twitter {
    width: 16px;
    height: 13px; } }

@media (max-width: 767px) {
  .social {
    justify-content: flex-start; }
  .social__link {
    width: auto;
    margin-left: 0;
    margin-right: 23px; } }

.footer {
  padding-top: 50px;
  padding-bottom: 39px;
  background-color: #011c40; }

.footer__wrapper {
  display: flex; }

.footer__logo {
  margin-top: 4px;
  margin-right: 32px; }

.footer__nav {
  flex-basis: 65%;
  margin-top: 6px; }

.footer__nav-list {
  justify-content: flex-start; }

.footer__nav-link {
  padding-right: 7px;
  padding-left: 7px; }

.footer__social {
  flex-basis: 25%;
  margin-left: auto; }

@media (max-width: 1023px) {
  .footer {
    padding-top: 40px;
    padding-bottom: 40px; }
  .footer__logo {
    margin-top: 10px;
    margin-right: 17px; }
  .footer__nav {
    flex-basis: 70%; }
  .footer__nav-link {
    font-size: 12px;
    line-height: 15px; }
  .footer__nav-link {
    margin-right: 10px; }
  .footer__social {
    flex-basis: 20%; } }

@media (max-width: 767px) {
  .footer {
    padding-top: 40px;
    padding-bottom: 40px; }
  .footer__wrapper {
    flex-direction: column; }
  .footer__logo {
    margin-top: 0; }
  .footer__nav {
    flex-basis: auto; }
  .footer__nav-link {
    color: #f9fbfd;
    padding-top: 9px;
    padding-left: 0;
    padding-bottom: 10px;
    margin-bottom: 2px;
    line-height: 14px; }
  .footer__social {
    flex-basis: auto;
    margin-left: 0; } }
